import React, { Component } from 'react'
import MyNavigation from './MyNavigation'
import Crossword from './CrosswordComponentsV2/Crossword'
import CrestGAN from './ArticleComponents/CrestGAN'
import TwitterFC from './ArticleComponents/TwitterFC'
import {Container, CssBaseline} from '@material-ui/core'
import CrosswordArticle from './ArticleComponents/CrosswordArticle';
import './Article.css'

export default class Portfolio extends Component {
    constructor(props){
        super(props);
        this.state = {project:'crossword'}
    }

    handleProjectDropdownChange = (e, popupState) => {
        popupState.close()
        this.setState({project:e.target.id})
    }
    render() {
        const articleStyle = {
            // top right bottom left 
            padding: "5% 5% 0px 20%"
          };
        var project;
        switch(this.state.project){
            case 'gan':
                project = 
                <> 
                    <CrestGAN/> 
                </> 
                break;
            case 'crossword':
                project = 
                <> 
                    <CrosswordArticle/> 
                    <hr/> 
                    <Crossword/>  
                </>
                break;
            case 'twfc':
                project = <TwitterFC/>
                break;
            default:
                project = <h1>Should not be seeing this</h1>
        }


        return (
            <>
                <CssBaseline/> 
                <MyNavigation article={this.state.project} handleProjectDropdownChange={this.handleProjectDropdownChange}/> 
                <Container style={articleStyle}>            
                    {project}
                </Container>
            </>
        )
    }
}
