import React from 'react';
import { ListItem, List } from "@material-ui/core";

export default function ClueList(props) {
    return (
      <List className="clues">
        <b style={{fontSize:"large",textAlign:"center"}}>{props.title}</b>
        <hr/>
        {props.clues.map((clue, index) => (
          <ListItem
            onClick={(e) => props.handleClueClick(e,props.isAcrossClues)}
            key={index}
            id={clue.number}
            className={
              (Number(clue.number) === props.currentClue) && (props.isAcross === props.isAcrossClues)
                ? "clue-highlighted"
                : ""
            }
          >
            <div id={clue.number} className="clue-number">{clue.number}</div>&nbsp;
            <div id={clue.number}>{clue.clue}</div>
          </ListItem>
        ))}
      </List>
    );
  }