import React from 'react';
import PropTypes from 'prop-types';
import {Typography, Toolbar, AppBar, CssBaseline, 
        Divider, Drawer, Hidden, IconButton, Tooltip,
        List, ListItem, Button, Menu, MenuItem,} 
        from '@material-ui/core'
import MenuIcon from '@material-ui/icons/Menu';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import GitHubIcon from '@material-ui/icons/GitHub';
import MailOutlineIcon from '@material-ui/icons/MailOutline';
import PopupState, { bindTrigger, bindMenu } from 'material-ui-popup-state';

const drawerWidth = 240;

const useStyles = makeStyles(theme => ({
  root: {
    display: 'flex',
  },
  drawer: {
    [theme.breakpoints.up('sm')]: {
      width: drawerWidth,
      flexShrink: 0,
    },

  },
  appBar: {
    [theme.breakpoints.up('sm')]: {
      width: `calc(100% - ${drawerWidth}px)`,
      marginLeft: drawerWidth,
      height:"9.2%",
    },
  },
  menuButton: {
    marginRight: theme.spacing(2),
    [theme.breakpoints.up('sm')]: {
      display: 'none',
    },
  },
  toolbar: theme.mixins.toolbar,
  drawerPaper: {
    width: drawerWidth,
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing(3),
  },
}));

function SectionFactory(title,list){
    return (
        <div>
          <List>
            <ListItem>
              <b>{title}</b>
            </ListItem>
            {list.map((text, index) => (
              <ListItem id={text} key={index}>
                  {text}
              </ListItem>
            ))}
            </List>
          <Divider />
        </div>
    )
}
function MyNavigation(props) {
  const { container } = props;
  const classes = useStyles();
  const theme = useTheme();
  const [mobileOpen, setMobileOpen] = React.useState(false);

  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };

  const drawer = (
    <div>
      <div className={classes.toolbar}/>
        <Divider />
        {SectionFactory("Student at Vanderbilt University",["B.E. in Computer Engineering","Minor in Engineering Mangement","Certified Associate in Project Management"])}
        {SectionFactory("Programming Languages I'm Familiar With",["Python","C++","SQL","JavaScript"])}
        {SectionFactory("Libraries, Frameworks, and Tools I'm Familiar With",["React","Django","Express","Pandas","Git and GitHub","SQL Server"])}
        {SectionFactory("Interests",["Crosswording","Jeopardy","Soccer","Chess"])}
    </div>
  );

  return (
    <div className={classes.root}>
      <CssBaseline />
      <AppBar position="fixed" className={classes.appBar}>
        <Toolbar>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            edge="start"
            onClick={handleDrawerToggle}
            className={classes.menuButton}
          >
            <MenuIcon />
          </IconButton>
          <Typography color="inherit" style={{ flex: 1 }}>
                        Dylan TerMolen's Portfolio 
                    </Typography>
                    <span>
                    <PopupState variant="popover" popupId="demo-popup-menu">
                        {popupState => (
                            <>
                                <Button variant="contained" color="primary" {...bindTrigger(popupState)}>
                                    See My Projects
                                </Button>
                                <Menu {...bindMenu(popupState)} >
                                    <MenuItem id="gan" style={props.article === 'gan' ? {backgroundColor:"#3f51b5", color:"white" } : {}} onClick={e => props.handleProjectDropdownChange(e,popupState)}>Crest GAN</MenuItem>
                                    <MenuItem id="twfc" style={props.article === 'twfc' ? {backgroundColor:"#3f51b5", color:"white" } : {}}onClick={e => props.handleProjectDropdownChange(e,popupState)}> Twitter FC</MenuItem>
                                    <MenuItem id="crossword" style={props.article === 'crossword' ? {backgroundColor:"#3f51b5", color:"white" } : {}}onClick={e => props.handleProjectDropdownChange(e,popupState)}>Crossword Interface</MenuItem>
                                </Menu>
                            </>
                        )}
                    </PopupState>
                      <Tooltip title="Check out my LinkedIn" arrow>
                          <IconButton target="_blank" href="https://www.linkedin.com/in/dylan-termolen-748048170/" color="inherit" aria-label="Save">
                              <LinkedInIcon />
                          </IconButton>
                        </Tooltip>
                        <Tooltip title="Check out my GitHub" arrow>
                          <IconButton target="_blank" href="https://github.com/Dylan-TerMolen" color="inherit" aria-label="More Options">
                              <GitHubIcon />
                          </IconButton>
                        </Tooltip>
                        <Tooltip title="Send me an e-mail" arrow>
                          <IconButton target="_blank" href="mailto:dylan.p.termolen@vanderbilt.edu" color="inherit" aria-label="More Options">
                              <MailOutlineIcon/>
                          </IconButton>
                        </Tooltip>

                    </span>
        </Toolbar>
      </AppBar>
      <nav className={classes.drawer} aria-label="mailbox folders">
        {/* The implementation can be swapped with js to avoid SEO duplication of links. */}
        <Hidden smUp implementation="css">
          <Drawer
            container={container}
            variant="temporary"
            anchor={theme.direction === 'rtl' ? 'right' : 'left'}
            open={mobileOpen}
            onClose={handleDrawerToggle}
            classes={{
              paper: classes.drawerPaper,
            }}
            ModalProps={{
              keepMounted: true, // Better open performance on mobile.
            }}
          >
            {drawer}
          </Drawer>
        </Hidden>
        <Hidden xsDown implementation="css">
          <Drawer
            classes={{
              paper: classes.drawerPaper,
            }}
            variant="permanent"
            open
          >
            {drawer}
          </Drawer>
        </Hidden>
      </nav>
    </div>
  );
}

MyNavigation.propTypes = {
  /**
   * Injected by the documentation to work in an iframe.
   * You won't need it on your project.
   */
  container: PropTypes.instanceOf(typeof Element === 'undefined' ? Object : Element),
};

export default MyNavigation;