import React, { Component } from 'react';

export default class Square extends Component {
    render() {
        let contents = (
            <span onClick={this.props.handlePositionChangeClick} id={this.props.index}>
              <span className="grid-clue-number" onClick={this.props.handlePositionChangeClick}>{
                (this.props.clueInfoArray[this.props.index]  === 0)|| this.props.clueInfoArray[this.props.index][0]
                }</span>
              {this.props.letter}
            </span>
          );
          let squareClass;
          if (this.props.isInCorrectMode){
            squareClass = this.props.isWrong ? "square square-wrong " : "square square-correct ";
          } else {
            squareClass = "square ";
          }
          if (this.props.letter === null) {
            squareClass += "square-black ";
            contents = null;
          } else if (this.props.index === this.props.userPosition) {
            squareClass += "square-user ";
          } else {
            if (this.props.isAcross) {
              squareClass += this.props.areHorizontallyAligned(
                this.props.index,
                this.props.userPosition
              )
                ? "square-in-row "
                : "";
            } else {
              squareClass += this.props.areVerticallyAligned(
                this.props.index,
                this.props.userPosition
              )
                ? "square-in-row "
                : "";
            }
          }
          return (
            <div
              key={this.props.index}
              id={this.props.index}
              className={squareClass}
              onClick={this.props.handlePositionChangeClick}
            >
              {contents}
            </div>
          );
        }
}
